body {
  color: #333;
  background-color: #fffde8;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.app-container {
  text-align: center;
  width: 100%;
  max-width: 1840px;
  margin: 0 auto;
}

main {
  margin: 20px;
}

.navbar {
  background-color: #fff9c4;
  border-radius: 8px;
  padding: 10px;
  box-shadow: 0 4px 8px #0000001a;
}

.navbar ul {
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  overflow: hidden;
}

.navbar a {
  color: #333;
  padding: 20px;
  font-size: 20px;
  text-decoration: none;
  transition: color .3s;
  display: block;
}

.navbar a:hover {
  background-color: #f8d06a;
}

.navbar li {
  margin: 0 15px;
}

h1 {
  text-align: center;
  margin-top: 50px;
  margin-bottom: 20px;
  font-family: Arial, sans-serif;
  font-size: 50px;
}

.auth-container {
  background-color: #fff;
  border-radius: 8px;
  max-width: 400px;
  margin: 2rem auto;
  padding: 2rem;
  box-shadow: 0 2px 4px #0000001a;
}

.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  margin-bottom: .5rem;
  font-weight: bold;
  display: block;
}

.form-group input {
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  padding: .5rem;
  font-size: 1rem;
}

.submit-btn {
  cursor: pointer;
  background-color: #f9e28e;
  border: none;
  border-radius: 4px;
  width: 100%;
  padding: .75rem;
  font-size: 1rem;
  font-weight: bold;
  transition: background-color .2s;
}

.submit-btn:hover {
  background-color: #f7d75c;
}

.auth-redirect {
  text-align: center;
  margin-top: 1rem;
}

.auth-redirect a {
  color: #f7d75c;
  font-weight: bold;
  text-decoration: none;
}

.auth-redirect a:hover {
  text-decoration: underline;
}

.welcome-section {
  background-color: #fff;
  border-radius: 12px;
  max-width: 600px;
  margin: 3rem auto;
  padding: 2rem;
  box-shadow: 0 4px 8px #0000001a;
}

.welcome-section h2 {
  color: #333;
  margin-bottom: 1rem;
  font-size: 2rem;
}

.welcome-text {
  font-size: 1.2rem;
  line-height: 1.6;
}

.welcome-text a {
  color: #f7d75c;
  font-weight: bold;
  text-decoration: none;
  transition: color .2s;
}

.welcome-text a:hover {
  color: #f8d06a;
  text-decoration: underline;
}

.book-section {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
}

.addLogForm {
  background-color: #fff;
  border-radius: 12px;
  margin-bottom: 2rem;
  padding: 2rem;
  box-shadow: 0 4px 8px #0000001a;
}

.input-group {
  gap: 1rem;
  margin-bottom: 1.5rem;
  display: flex;
}

.input-group input {
  border: 1px solid #ddd;
  border-radius: 6px;
  flex: 1;
  padding: .75rem;
  font-size: 1rem;
}

.input-group input:focus {
  border-color: #f7d75c;
  outline: none;
  box-shadow: 0 0 0 2px #f7d75c33;
}

.rating {
  text-align: center;
  margin: 1.5rem 0;
}

.rating-label {
  color: #666;
  margin-bottom: .5rem;
  font-size: 1.1rem;
}

.egg-container {
  justify-content: center;
  gap: .5rem;
  margin: 1rem 0;
  display: flex;
}

.egg {
  cursor: pointer;
  opacity: .8;
  width: 40px;
  height: 40px;
  transition: transform .2s, opacity .2s;
}

.egg:hover {
  opacity: 1;
  transform: scale(1.1);
}

.egg.selected {
  opacity: 1;
}

.book-list {
  margin-top: 2rem;
}

@media (width <= 768px) {
  .input-group {
    flex-direction: column;
    gap: .75rem;
  }

  .welcome-section, .book-section {
    margin: 1rem;
    padding: 1rem;
  }

  .egg {
    width: 30px;
    height: 30px;
  }
}
